import React from 'react';
import { DataGrid } from '@mui/x-data-grid';

function AssetsGrid({portfolios}) {

  const columns = [ { field: 'currency', headerName: 'Currency', width: 150, renderCell: (params) => params.value}, { field: 'amount', headerName: 'Amount', width: 150, }  ];

  return (
    <div style={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={portfolios}
        columns={columns}
        pageSize={5}
        rowsPerPageOptions={[5]}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
}

export default AssetsGrid;
