/* eslint-disable react/no-typos */

import React, { useEffect, useState } from 'react';
import css from './secondaryNavMenu.module.css';
import Card from '../Card/Card';
import PropTypes from 'prop-types';
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';

function SecondaryNavMenu({
  deleteAllPortfoliosCallback,
  arePortfoliosActive
}) {
  const [showEditMenu, setShowEditMenu] = useState(false);


  const portfoliosMenu = (
      <div
        onClick={() => deleteAllPortfoliosCallback()}
        className={css.actionButton}
      >
        Delete all portfolios
      </div>
    );
  if(!arePortfoliosActive) return null;
  return (
    <div className={css.menuContainer}>
      <Card>
          <div className={css.secondaryNavMenuContainerClosed}>
            {showEditMenu ?  portfoliosMenu: null}
              <div
                onClick={() => setShowEditMenu(!showEditMenu)}
                className={css.editButton}
              >

                <div className={css.editText}>Edit Portfolios</div>
                {showEditMenu ? <MenuOpenIcon /> : <MenuIcon /> }
              </div>
          </div>
      </Card>
    </div> 
  );
}

SecondaryNavMenu.propTypes = {
  deleteAllPortfoliosCallback: PropTypes.func,
  arePortfoliosActive: PropTypes.bool,
}

SecondaryNavMenu.defaultProps = {
  deleteAllPortfoliosCallback: () => {},
}

export default SecondaryNavMenu;
